import React, { Component } from 'react'
import { auth } from '../helpers/auth'

function setErrorMsg(error) {
  return {
    registerError: error
  }
}

export default class Register extends Component {
  state = { registerError: null }
  handleSubmit = (e) => {
    e.preventDefault()
    if (this.pw.value.length < 6){
      this.setState(setErrorMsg("La contraseña debe ser mínimo de 6 caracteres."))
    }
    else if (!this.email.value.endsWith("@itesm.mx")){
      this.setState(setErrorMsg("Sólo se pueden registrar correos '@itesm.mx'."))
    }
    else if (this.pw.value !== this.pwc.value){
      this.setState(setErrorMsg("Las contraseñas no coinciden."))
    } else{
      auth(this.email.value, this.pw.value, this.usrn.value)
      .catch(e => this.setState(setErrorMsg(e.message)))
    }
  }
  render () {
    return (
      <div className="col-sm-6 col-sm-offset-3">
        <h1>Register</h1>
        <form onSubmit={this.handleSubmit}>
          <div className="form-group">
            <label>Nombre</label>
            <input type="userName" className="form-control" placeholder="Nombre" ref={(usrn) => this.usrn = usrn} />
          </div>
          <div className="form-group">
            <label>Correo</label>
            <input className="form-control" ref={(email) => this.email = email} placeholder="Correo"/>
          </div>
          <div className="form-group">
            <label>Contraseña</label>
            <input type="password" className="form-control" placeholder="Contraseña" ref={(pw) => this.pw = pw} />
          </div>
          <div className="form-group">
            <label>Confirmar Contraseña</label>
            <input type="password" className="form-control" placeholder="Contraseña" ref={(pwc) => this.pwc = pwc} />
          </div>
          {
            this.state.registerError &&
            <div className="alert alert-danger" role="alert">
              <span className="glyphicon glyphicon-exclamation-sign" aria-hidden="true"></span>
              <span className="sr-only">Error:</span>
              &nbsp;{this.state.registerError}
            </div>
          }
          <button type="submit" className="btn btn-primary">Registrar</button>
        </form>
      </div>
    )
  }
}



